/* // Descripcion ----------------------------------------------------------- */
// Activadores para clases useful.
//
// #activator #background #border #class #container #flexbox #list #margin
// #padding #responsive #text #useful

// REF [05]
// REF [38*] ContainerMAX width


/* // Listado --------------------------------------------------------------- */
// 01 _ $classes_container_act
// 02 _ $helpers_spacing_act
// 03 _ $classes_position_display_mask_cursor_act
// 04 _ $classes_flexbox_act
// 05 _ $classes_lists_floats_act
// 06 _ $classes_text_act
// 07 _ $classes_bg_bd_NO_act
//
//
// 01 _ $classes_container_act
// Activa los if que se encuentran en  `[/etc/css/scss/settings_container.scss]`
// Genera los margenes negativos para cada media query para la clase
// `.container_centered_absolute` que se usa junto con la clase `.container`
//
// Ej. con $classes_container_act_A500
// @media (min-width: 500px) {
//     .container.container_centered_absolute_A500{
//         position: absolute;
//         left: 50%;
//         margin-left: calc(-1 * calc(85% / 2));
//     }
// }
// @media (min-width: 767px) {
//     .container.container_centered_absolute_A500{
//         margin-left: calc(-1 * calc(720px / 2));
//     }
// }
// @media (min-width: 1024px) {
//     .container.container_centered_absolute_A500{
//         margin-left: calc(-1 * calc(900px / 2));
//     }
// }
// @media (min-width: 1200px) {
//     .container.container_centered_absolute_A500{
//         margin-left: calc(-1 * calc(900px / 2));
//     }
// }
// @media (min-width: 1600px) {
//     .container.container_centered_absolute_A500{
//         margin-left: calc(-1 * calc(900px / 2));
//     }
// }
//
// REF [38*] ContainerMAX width
// Al modificarse el valor de la variable `$containerMAX_value` en el archivo
// `[/src/var/global.var.inc.php]` hay que activar la variable correspondiente
// en este archivo

// Este valor luego debe ser modificado en el archivo
// `[/etc/css/custom/var/container_sizes_var.scss]`
// declarado en la variable `$containerMAX_value`
//
//
// 02 _ $helpers_spacing_act
// 03 _ $classes_position_display_mask_cursor_act
// 04 _ $classes_flexbox_act
// 05 _ $classes_lists_floats_act
// Activan los if que se encuentran en
// `[/etc/css/scss/classes_useful_all.scss]` y
// `[/etc/css/scss/classes_useful_responsive.scss]`
//
// Genera clases para spacing helpers, positiones, display, mascaras, flexbox y
// listas.
//
//
// 06 _ $classes_text_act
// 07 _ $classes_bg_bd_NO_act
// Activan los if que se encuentran en `[/etc/css/scss/classes_useful_responsive.scss]`
//
// Activa clases responsive para texto y valores nulos para bordes y fondos.



/* // Referencias globales de nombres de variables y mixins ----------------- */
///-- Inicio del nombre
// bd                   = borde
// bg                   = fondo / background
// bx                   = box
// ft                   = fuente
// mq, MQ               = media query @media
// tx                   = texto

///-- Fin del nombre
// _before              = se usa con pseudo-elementos ::before
// _after               = se usa con pseudo-elementos ::after
// _act                 = activacion
// _ani                 = animacion
// _hover               = se usa con pseudo-clases :focus y :hover
// _hs                  = hash
// _imp			= importacion
// _mx                  = mixin
// _var                 = variables

///-- Espaciado
// P, M                 = padding, margin
// T, R, B, L           = top, right, bottom, left
// H, V, A, X           = horizontal, vertical, all, custom defined

///-- Tamanos de Pantalla
// T                    = tablet
// B, A, O, L           = before, after, only, landscape

///-- Colores
// X, L, D              = extra, light, dark
// De, S, B             = desaturated, semi, bright

///-- Fuentes
// T, L, M, R, B, BK    = thin, light, medium, regular, bold, black
// X, S, U, C, I        = extra, semi, ultra, condensed, italic



