/* * NombreDeProyecto * ========================================================
   Form Fonts Variables | Custom [/etc/css/custom/var/form_fonts_var.scss]
   ========================================================================== */

@use "env_var" as *;

@use "text_var" as *;
@use "fonts_main_var" as *;

@use "sass:color";
@use "sass:map";
/* // Referencias globales de nombres de variables y mixins ----------------- */
///-- Inicio del nombre
// bd                   = borde
// bg                   = fondo / background
// bx                   = box
// ft                   = fuente
// mq, MQ               = media query @media
// tx                   = texto

///-- Fin del nombre
// _mx                  = mixin
// _act                 = activacion
// _ani                 = animacion
// _hs                  = hash
// _hover               = se usa con pseudo-clases :focus y :hover
// _before              = se usa con pseudo-elementos ::before
// _after               = se usa con pseudo-elementos ::after

///-- Espaciado
// P, M                 = padding, margin
// T, R, B, L           = top, right, bottom, left
// H, V, A, X           = horizontal, vertical, all, custom defined

///-- Tamanos de Pantalla
// T                    = tablet
// B, A, O, L           = before, after, only, landscape

///-- Colores
// X, L, D              = extra, light, dark
// De, S, B             = desaturated, semi, bright

///-- Fuentes
// T, L, M, R, B, BK    = thin, light, medium, regular, bold, black
// X, S, U, C, I        = extra, semi, ultra, condensed, italic



/* // Form Label ------------------------------------------------------------ */
$form_label_ftFamily:           $ftFamily_regular_global;
$form_label_ftWeight:           400; /* // REF [01] en `[/etc/css/custom/act/fonts_act.scss]`*/

/* // Form Input ------------------------------------------------------------ */
$form_input_ftFamily:           $ftFamily_regular_global;
$form_input_ftWeight:           400; /* // REF [01] en `[/etc/css/custom/act/fonts_act.scss]`*/

/* // Form Button Submit // REF [21] ---------------------------------------- */
$button_form_submit_ftFamily:   $form_label_ftFamily;
$button_form_submit_ftWeight:   400; /* // REF [01] en `[/etc/css/custom/act/fonts_act.scss]`*/

/* // Form status message --------------------------------------------------- */
$form_pop_title_ftFamily:       $form_label_ftFamily;

/* // Form validation ------------------------------------------------------- */
$form_span_invalid_ftFamily:    $arial;
    
     
/* // Form span msg / sugg -------------------------------------------------- */
$form_span_sugg_ftFamily:       $form_span_invalid_ftFamily;
$form_span_anchor_ftFamily:     $form_span_sugg_ftFamily;